a,
abbr,
acronym,
address,
applet,
article,
aside,
audio,
b,
big,
blockquote,
body,
canvas,
caption,
center,
cite,
code,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
embed,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
i,
iframe,
img,
ins,
kbd,
label,
legend,
li,
mark,
menu,
nav,
object,
ol,
output,
p,
pre,
q,
ruby,
s,
samp,
section,
small,
span,
strike,
strong,
sub,
summary,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
tt,
u,
ul,
var,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	font-family: Calibri, sans-serif;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}
body {
	line-height: 1;
}
ol,
ul {
	list-style: none;
}
blockquote,
q {
	quotes: none;
}
blockquote:after,
blockquote:before,
q:after,
q:before {
	content: "";
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
* {
	margin: 0;
	padding: 0;
}
h1 {
	font-size: 8vh !important;
	color: #fffaf0;
	text-align: center;
}
path:hover {
	stroke: #ffb4d9;
	stroke-width: 1.5px;
}
p {
	font-size: 30px;
	color: #fffaf0;
	text-align: center;
	padding-top: 25px;
}
table {
	background-color: #161625 !important;
}
th {
	padding: 20px !important;
	background-color: #212230;
	border-color: #69625b !important;
	border-radius: 0;
}
th:focus {
	outline: #161625 !important;
}
td {
	text-align: right;
	padding-left: 15px;
	padding-right: 15px;
	border-radius: 0;
}
tr td {
	background-color: #161625;
}
tr:nth-child(2n) td {
	background-color: #212230;
}
tr:nth-child(2n) td,
tr:nth-child(odd) td {
	border-color: inherit;
}
td:nth-child(2),
th:nth-child(2) {
	color: #8f6eb2;
	text-align: left;
}
td:nth-child(3),
th:nth-child(3) {
	color: #fe073a;
	text-align: center;
}
td:nth-child(4),
th:nth-child(4) {
	color: #007bfe;
	text-align: center;
}
td:nth-child(5),
th:nth-child(5) {
	color: #28a645;
	text-align: center;
}
td:last-child,
th:last-child {
	color: #6c757d;
	text-align: center;
}
td {
	padding-top: 7px !important;
	padding-bottom: 7px !important;
}
.about p {
	text-align: left;
	font-size: 30px;
	padding-left: 15px;
	padding-right: 15px;
	line-height: 40px;
}
.about img,
.about p {
	padding-top: 55px;
}
